*{
    margin: 0px;
    padding: 0px
}
@import url("https://fonts.google.com/specimen/Manrope");

body{
    background-color: hsl(218, 23%, 16%);
}



a{
    text-decoration: none;
    color: aqua
}

.tipCont{
    margin: auto;
    margin-top: 8rem;
    border: 2px solid transparent;
    width: 65%;
    height: max-content;
    background-color: hsl(217, 19%, 24%);
    color: hsl(193, 38%, 86%);
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

#id{
    text-align: center;
    margin: 20px;
    color: hsl(150, 100%, 66%);
}

.tip{
    text-align: center;
    margin: 30px;
    font-size: 20px;
    font-family: "Manrope", Arial;
}

.adorno{
    margin-top: 50px;
}

.boton {
    position: relative;
    top: 1.5rem
}
#boton{
    text-align: center;
    border-radius: 100%;
    display: flex;
    border: 0px solid transparent;
    width: 50px;
    height: 50px;
    cursor: pointer;
}
#boton:hover {
    background-color:hsl(150, 100%, 66%); ;
}
svg{
  margin: auto;
}